import React from 'react';
import Button from "../Button/Button";
import './input-button.css'

const InputButton = ({text, setText, placeholder, onClick, btnText}) => {
    return (
        <div className='input-button'>
            <input type="text" value={text} placeholder={placeholder} onChange={(e) => setText(e.target.value)}/>
            <Button type={'primary'} onClick={onClick}>{btnText}</Button>
        </div>
    );
};

export default InputButton;