import {createBrowserRouter} from "react-router-dom";
import App from "../App";
import RootPage from "../pages/root-page/RootPage";

const router = createBrowserRouter([
    {
        path: '/',
        element: <App/>,
        children: [
            {
                element: <RootPage/>,
                index: true
            },
            {
                path: '/warranty',
                element: <div>Warranty</div>
            },
            {
                path: '/about',
                element: <div>About us</div>
            },
        ]
    },
])

export default router