import React, {useState} from 'react';
import InputButton from "../UI/input-button/InputButton";
import ProductWarrantyInfo from "../UI/product-warranty-info/productWarrantyInfo";
import './warranty-form.css'

const WarrantyForm = () => {
    const [serial, setSerial] = useState('')
    const [info, setInfo] = useState({})

    const setInfoHandler = (e) => {
        e.preventDefault()

        setInfo({
            serial: '4UY11323MT',
            product: 'HP Pavilion Gaming 15-ec1000 Laptop PC',
            startDate: '2023-08-05T19:46:34.275+00:00',
            duration: 1
        })
    }

    return (
        <form className={'warranty-form'}>
            <div className="warranty-form__text">
                <h1>Verify your Product</h1>
                <p>Enter the serial number of the product to check the warranty, if the product is not registered, then you can register it</p>
            </div>
            <div className="warranty-form__form">
                <InputButton text={serial} setText={setSerial} btnText={'check'} placeholder={'Enter serial number...'} onClick={(e) => setInfoHandler(e)}/>
                {info.serial && <ProductWarrantyInfo serial={info.serial} product={info.product} startDate={info.startDate} duration={info.duration}/>}
            </div>
        </form>
    );
};

export default WarrantyForm;